<template>
  <div class="Expander">
    <div class="row">
      <div class="col">
        <div class="tabs">
          <div class="tab">
            <input type="checkbox" :id="id" />
            <label class="tab-label p-5" :for="id" :class="titleClass">
              <slot name="title"></slot>
            </label>
            <div class="tab-content">
              <slot name="content"></slot>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    titleClass: String
  },
  data() {
    return {};
  },
  methods: {
    uid: function() {
      return (
        "_" +
        Math.random()
          .toString(36)
          .substr(2, 9)
      );
    }
  },
  created() {
    this.id = this.uid();
  }
};
</script>

