<template>
  <div class="home sm:mx-0 md:mx-5 xl:mx-64">
    <div
      class="status-loading w-full h-full fixed block top-0 left-0 bg-white opacity-75 z-50"
      :class="{ hidden: this.loading == 0 }"
    >
      <span class="text-green-500 opacity-75 mx-auto my-2 block relative" style="
    top: 50%;
">
        <i class="fas fa-circle-notch fa-spin fa-5x"></i>
      </span>
    </div>
    <div
      class="status-error flex rounded items-center sticky top-0 bg-red-300 text-red-800 px-2 py-4 md:w-2/3 m-auto text-left"
      :class="{ hidden: this.errors.length == 0 }"
    >
      <ul>
        <li v-for="(error, index) in errors" :key="index">{{ error.msg }}</li>
      </ul>
    </div>

    <h1 class="text-4xl font-bold">{{ site.title }}</h1>
    <div
      class="status-success flex rounded items-center text-center bg-green-300 text-green-800 px-2 py-4 md:w-2/3 m-auto text-left"
      :class="{ hidden: !this.finish }"
    >
      <div class="m-auto text-xl">{{ finish }}</div>
    </div>

    <div v-if="!finish">
      <h2 class="text-lg text-gray-600 mb-3">Obedové menu - {{ startdate }} - {{ enddate }}</h2>
      <div
        class="flex flex-col md:w-2/3 m-auto px-6 py-4 mb-2 border-solid border-2 border-gray-200 rounded"
      >
        <div
          class="status-loading w-full h-full block top-0 left-0 bg-white opacity-75 z-50"
          :class="{ hidden: this.loadingPages == 0 }"
        >
          <span class="text-green-500 opacity-75 mx-auto my-2 block relative" style="top: 50%;">
            <i class="fas fa-circle-notch fa-spin fa-5x"></i>
          </span>
        </div>
        <div class="text-xs text-left" v-html="pages.instructions"></div>
      </div>
      <div class="flex flex-col md:w-2/3 m-auto border border-gray-200 rounded">
        <div
          class="status-loading w-full h-full block top-0 left-0 bg-white opacity-75 z-50"
          :class="{ hidden: this.loadingOffer == 0 }"
        >
          <span class="text-green-500 opacity-75 mx-auto my-2 block relative" style="top: 50%;">
            <i class="fas fa-circle-notch fa-spin fa-5x"></i>
          </span>
        </div>
        <template v-for="(day,index) in offer">
          <DailyMenu
            v-if="(config.showPastDays || day.number >= new Date().getDay()) && day.meals.length > 0"
            :key="index"
            :dailymenu="day"
            :first="(index == 0) ? 1 : 0"
            :last="(index == offer.length - 2) ? 1 : 0"
            @addOrderItem="addOrderItem"
            @removeOrderItem="removeOrderItem"
          />
        </template>
      </div>

      <template v-if="initerror == 0">
        <h2 class="text-lg text-gray-600 mt-5 mb-5">Kontaktné údaje</h2>
        <div class="md:w-2/3 m-auto text-left">
          <form action>
            <div class="flex flex-wrap">
              <div class="w-full md:w-1/3 px-3 mb-4">
                <label
                  class="block uppercase tracking-wide text-gray-700 text-xs font-bold mb-2"
                  for="customer[name]"
                >
                  Meno a priezvisko
                  <span class="text-red-600 text-xs font-bold ml-1">*</span>
                </label>
                <input
                  v-on:change="revalidate()"
                  class="appearance-none block w-full bg-gray-200 text-gray-700 border rounded py-3 px-4 mb-0 leading-tight focus:bg-white"
                  v-model="customer.fullname"
                  :class="{ 'border-red-500': hasError('customer.fullname') }"
                />
                <p
                  v-if="hasError('customer.fullname')"
                  class="text-red-500 text-xs italic"
                >{{ getError("customer.fullname") }}</p>
              </div>

              <div class="w-full md:w-1/3 px-3 mb-4">
                <label
                  class="block uppercase tracking-wide text-gray-700 text-xs font-bold mb-2"
                  for="customer[phone]"
                >
                  Telefón
                  <span class="text-red-600 text-xs font-bold ml-1">*</span>
                </label>
                <input
                  v-on:change="revalidate()"
                  class="appearance-none block w-full bg-gray-200 text-gray-700 border borer-red-500 rounded py-3 px-4 mb-0 leading-tight focus:bg-white"
                  v-model="customer.phone"
                  :class="{ 'border-red-500': hasError('customer.phone') }"
                />
                <p
                  v-if="hasError('customer.phone')"
                  class="text-red-500 text-xs italic"
                >{{ getError("customer.phone") }}</p>
              </div>

              <div class="w-full md:w-1/3 px-3 mb-4">
                <label
                  class="block uppercase tracking-wide text-gray-700 text-xs font-bold mb-2"
                  for="customer[email]"
                >
                  Email
                  <span class="text-red-600 text-xs font-bold ml-1">*</span>
                </label>
                <input
                  v-on:change="revalidate()"
                  class="appearance-none block w-full bg-gray-200 text-gray-700 border borer-red-500 rounded py-3 px-4 mb-0 leading-tight focus:bg-white"
                  v-model="customer.email"
                  :class="{ 'border-red-500': hasError('customer.email') }"
                />
                <p
                  v-if="hasError('customer.email')"
                  class="text-red-500 text-xs italic"
                >{{ getError("customer.email") }}</p>
              </div>

              <div class="w-full md:w-1/2 px-3 mb-4">
                <span
                  class="uppercase tracking-wide text-gray-700 text-xs font-bold"
                >Spôsob doručenia</span>
                <span class="text-red-600 text-xs font-bold ml-1">*</span>
                <div class="flex mt-2">
                  <div class="mr-2">
                    <label class="inline-flex items-center">
                      <input
                        v-model="shipping.method"
                        type="radio"
                        class="form-radio"
                        name="shipping.method"
                        :value="SHIPPING_METHOD_DELIVERY"
                        v-on:change="revalidate()"
                        checked
                      />
                      <span class="ml-2">Donáška</span>
                    </label>
                  </div>
                  <div>
                    <label class="inline-flex items-center">
                      <input
                        v-model="shipping.method"
                        type="radio"
                        class="form-radio"
                        name="shipping.method"
                        :value="SHIPPING_METHOD_TAKEAWAY"
                        v-on:change="revalidate()"
                      />
                      <span class="ml-2">Osobný odber</span>
                    </label>
                  </div>
                </div>
                <p
                  v-if="hasError('shipping.method')"
                  class="text-red-500 text-xs italic"
                >{{ getError("shipping.method") }}</p>
              </div>

              <div class="w-full md:w-1/2 px-3 mb-4">
                <span class="uppercase tracking-wide text-gray-700 text-xs font-bold">Spôsob platby</span>
                <span class="text-red-600 text-xs font-bold ml-1">*</span>
                <div class="flex mt-2">
                  <div class="mr-2">
                    <label class="inline-flex items-center">
                      <input
                        v-model="payment.method"
                        type="radio"
                        class="form-radio"
                        name="payment.method"
                        :value="PAYMENT_METHOD_CARD_TAKEAWAY"
                        v-on:change="revalidate()"
                        checked
                      />
                      <span class="ml-2">Kartou pri odbere</span>
                    </label>
                  </div>
                  <div>
                    <label class="inline-flex items-center">
                      <input
                        v-model="payment.method"
                        type="radio"
                        class="form-radio"
                        name="payment.method"
                        :value="PAYMENT_METHOD_CASH"
                        v-on:change="revalidate()"
                      />
                      <span class="ml-2">V hotovosti</span>
                    </label>
                  </div>
                </div>
                <p
                  v-if="hasError('payment.method')"
                  class="text-red-500 text-xs italic"
                >{{ getError("payment.method") }}</p>
              </div>

              <div class="w-full px-3 mb-4" :class="{ hidden: shipping.method == 2 }">
                <label
                  class="block uppercase tracking-wide text-gray-700 text-xs font-bold mt-2 mb-2"
                  for="customer[name]"
                >
                  Adresa pre doručenie
                  <span class="text-red-600 text-xs font-bold ml-1">*</span>
                </label>
                <input
                  v-on:change="revalidate()"
                  class="appearance-none block w-full bg-gray-200 text-gray-700 border rounded py-3 px-4 mb-0 leading-tight focus:bg-white"
                  v-model="shipping.address"
                  :class="{ 'border-red-500': hasError('shipping.address') }"
                />
                <p
                  v-if="hasError('shipping.address')"
                  class="text-red-500 text-xs italic"
                >{{ getError("shipping.address") }}</p>
              </div>

              <div class="w-full px-3 mb-4">
                <label
                  class="block uppercase tracking-wide text-gray-700 text-xs font-bold mb-2"
                  for="note"
                >Poznámka</label>
                <textarea
                  v-on:change="revalidate()"
                  class="appearance-none block w-full bg-gray-200 text-gray-700 border rounded py-3 px-4 mb-3 h-32 leading-tight focus:bg-white"
                  v-model="note"
                  :class="{ 'border-red-500': hasError('note') }"
                  maxlength="255"
                  placeholder="napr. čas doručenia a pod."
                />
                <p
                  v-if="hasError('note')"
                  class="text-red-500 text-xs italic"
                >{{ getError("note") }}</p>
              </div>
            </div>
          </form>
        </div>
        <div
          class="flex rounded items-center sticky bottom-0 bg-orange-200 px-2 py-4 mt-4 md:w-2/3 m-auto"
        >
          <div>
            <div class="inline-flex mr-1">Spolu:</div>
            <div class="inline-flex font-bold">€ {{ total.toFixed(2) }}</div>
            <div
              class="inline-flex mr-1 text-gray-700 text-xs ml-2"
            >+ cena za donášku mimo bezplatných lokalít</div>
          </div>
          <div class="flex-grow"></div>
          <div class>
            <button
              @click="submitOrder"
              class="bg-orange-900 text-white font-extrabold px-6 py-4 text-xl rounded"
            >Objednať</button>
          </div>
        </div>
      </template>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import axios from "axios";
import DailyMenu from "@/components/DailyMenu.vue";

var moment = require("moment");
moment.locale("sk");

export default {
  name: "Home",
  components: {
    DailyMenu
  },
  methods: {
    moment: function() {
      return moment();
    },
    submitOrder: function() {
      if (this.validate()) {
        this.loading = 1;
        axios
          .post(this.config.backendbaseurl + "/order/submit", {
            orderitems: this.orderitems,
            customer: this.customer,
            shipping: this.shipping,
            payment: this.payment,
            note: this.note
          })
          .then(response => {
            if (response.status != 200) {
              // Business error
              console.log(response);
              this.setError(response.data.error);
            } else {
              this.setFinish("Ďakujeme, Vaša objednávka bola zaznamenaná.");
              this.clear();
            }
            this.loading = 0;
          })
          .catch(error => {
            // Technical error
            console.log(error);
            this.setError(
              "Vyskytla sa chyba pri odosielaní objednávky. Skontrolujte prosim Vaše internetové pripojenie. V prípade ak problém pretrváva, kontaktujte nás telefonicky."
            );
            this.loading = 0;
          });
      }
    },
    revalidate: function() {
      if (this.errors.length > 0) {
        this.validate();
      }
    },
    clear: function() {
      this.customer.fullname = null;
      this.customer.phone = null;
      this.shipping.method = null;
      this.payment.method = null;
      this.note = null;

      this.offer.forEach(function(dailymenu) {
        dailymenu.meals.forEach(function(meal) {
          meal.qty = 0;
          meal.sumprice = 0;
        });
      });
    },
    validate: function() {
      var error = null;
      this.errors = [];

      if (this.total == 0) {
        error = {
          fieldname: "total",
          msg: "Vyberte jedlo."
        };
        this.errors.push(error);
      }

      if (
        this.shipping.method == this.SHIPPING_METHOD_DELIVERY &&
        this.total < this.config.minorder
      ) {
        error = {
          fieldname: "total",
          msg:
            "Minimálna cena objednávky pre donášku je € " + this.config.minorder
        };
        this.errors.push(error);
      }

      if (!this.customer.fullname) {
        error = {
          fieldname: "customer.fullname",
          msg: "Meno a priezvisko je povinné"
        };
        this.errors.push(error);
      }

      if (!this.customer.phone) {
        error = {
          fieldname: "customer.phone",
          msg: "Telefón je povinný"
        };
        this.errors.push(error);
      }

      if (!this.customer.email) {
        error = {
          fieldname: "customer.email",
          msg: "Email je povinný"
        };
        this.errors.push(error);
      }

      if (!this.shipping.method) {
        error = {
          fieldname: "shipping.method",
          msg: "Vyberte spôsob doručenia"
        };
        this.errors.push(error);
      } else if (this.shipping.method == this.SHIPPING_METHOD_DELIVERY) {
        if (!this.shipping.address) {
          error = {
            fieldname: "shipping.address",
            msg: "Zadajte adresu pre doručenie"
          };
          this.errors.push(error);
        }
      }

      if (!this.payment.method) {
        error = {
          fieldname: "payment.method",
          msg: "Vyberte spôsob platby"
        };
        this.errors.push(error);
      }

      return this.errors.length == 0;
    },
    hasError: function(fieldname) {
      return this.errors.find(error => error.fieldname === fieldname);
    },
    getError: function(fieldname) {
      return this.errors.find(error => error.fieldname === fieldname).msg;
    },
    setError: function(msg) {
      var error = {
        fieldname: null,
        msg: msg
      };

      this.errors.push(error);
    },
    setFinish(msg) {
      this.finish = msg;
    },
    addOrderItem: function(item) {
      item.qty += 1;
      item.sumprice = item.qty * item.unitprice;

      this.revalidate();
    },
    removeOrderItem: function(item) {
      if (item.qty > 1) {
        item.qty -= 1;
      } else {
        item.qty = 0;
      }
      item.sumprice = item.qty * item.unitprice;

      this.revalidate();
    },
    initoffer: function() {
      this.loadingOffer = 1;
      axios
        .get(this.config.backendbaseurl + "/offer")
        .then(response => {
          this.days.forEach(day => {
            var dailymenu = {
              number: day.number,
              name: day.friendlyName,
              meals: []
            };
            response.data.forEach(meal => {
              if (meal.day === day.key) {
                meal.qty = 0;
                meal.sumprice = 0;
                dailymenu.meals.push(meal);
              }
            });
            this.offer.push(dailymenu);
          });

          this.loadingOffer = 0;
        })
        .catch(error => {
          // Technical error
          console.log(error);
          this.setError(
            "Vyskytla sa chyba pri načítaní ponuky jedál. Skontrolujte prosim Vaše internetové pripojenie. V prípade ak problém pretrváva, kontaktujte nás telefonicky."
          );
          this.initerror = 1;
          this.loadingOffer = 0;
        });
    },
    fetchpages: function() {
      this.loadingPages = 1;
      axios
        .get(this.config.backendbaseurl + "/pages")
        .then(response => {
          for (var key in response.data) {
            var parser = new DOMParser();
            var doc = parser.parseFromString(response.data[key], "text/html");
            var body = doc.getElementsByTagName("body")[0].innerHTML;
            this.pages[key] = body;
          }
          this.loadingPages = 0;
        })
        .catch(error => {
          // Technical error
          console.log(error);
          this.setError(
            "Vyskytla sa chyba pri načítaní stránky. Skontrolujte prosim Vaše internetové pripojenie. V prípade ak problém pretrváva, kontaktujte nás telefonicky."
          );
          this.initerror = 1;
          this.loadingPages = 0;
        });
    }
  },
  computed: {
    total() {
      var total = 0;
      this.offer.forEach(function(dailymenu) {
        dailymenu.meals.forEach(function(meal) {
          total += meal.qty * meal.unitprice;
        });
      });
      return total;
    },
    orderitems() {
      var orderitems = [];

      this.offer.forEach(day => {
        day.meals.forEach(meal => {
          if (meal.qty > 0) {
            var orderitem = {
              id: meal.id,
              qty: meal.qty
            };
            orderitems.push(orderitem);
          }
        });
      });

      return orderitems;
    },
    startdate() {
      var startday = 1; // Monday

      return this.moment()
        .subtract(moment().day() - startday, "days")
        .format("L");
    },
    enddate() {
      var endday = 5; // Friday
      return this.moment()
        .add(endday - moment().day(), "days")
        .format("L");
    }
  },
  created() {
    this.SHIPPING_METHOD_DELIVERY = 1;
    this.SHIPPING_METHOD_TAKEAWAY = 2;
    this.PAYMENT_METHOD_CARD_TAKEAWAY = 1;
    this.PAYMENT_METHOD_CASH = 2;
  },
  mounted() {
    this.fetchpages();
    this.initoffer();
  },
  data: () => ({
    loading: 0,
    loadingPages: 0,
    loadingOffer: 0,
    finish: null,
    config: {
      minorder: 10,
      backendbaseurl: process.env.VUE_APP_BACKEND_BASE_URL,
      showPastDays: process.env.VUE_APP_SHOW_PAST_DAYS
    },
    days: [
      {
        number: 0,
        key: "Nedela",
        friendlyName: "Nedeľa"
      },
      {
        number: 1,
        key: "Pondelok",
        friendlyName: "Pondelok"
      },
      {
        number: 2,
        key: "Utorok",
        friendlyName: "Utorok"
      },
      {
        number: 3,
        key: "Streda",
        friendlyName: "Streda"
      },
      {
        number: 4,
        key: "Stvrtok",
        friendlyName: "Štvrtok"
      },
      {
        number: 5,
        key: "Piatok",
        friendlyName: "Piatok"
      },
      {
        number: 6,
        key: "Sobota",
        friendlyName: "Sobota"
      }
    ],
    offer: [],
    customer: {
      fullname: null,
      phone: null,
      email: null
    },
    shipping: {
      method: null,
      address: null
    },
    payment: {
      method: null
    },
    note: null,
    initerror: [],
    errors: [],
    site: {
      title: "Lokal Unas"
    },
    pages: {}
  })
};
</script>
