<script>
import Expander from "@/components/Expander.vue";

var moment = require("moment");
moment.locale("sk");

export default {
  name: "DailyMenu",
  components: {
    Expander
  },
  props: {
    dailymenu: Object,
    first: Number,
    last: Number
  },
  methods: {
    moment: function() {
      return moment();
    },
    addOrderItem: function(item) {
      this.$emit("addOrderItem", item);
    },
    removeOrderItem: function(item) {
      this.$emit("removeOrderItem", item);
    }
  }
};
</script>

